@use "./variables" as v;


@mixin respond($breakpoint) {
    @if $breakpoint == bp-sm{
        @media only screen and (min-width: 37.5em) { @content };    //600px
    }
    @if $breakpoint == bp-md {
        @media only screen and (min-width: 56.25em) { @content };     //900px
    }
    @if $breakpoint == bp-lg {
        @media only screen and (min-width: 75em) { @content };    //1200px
    }
    @if $breakpoint == bp-xl{
        @media only screen and (min-width: 112.5em) { @content };    //1800
    }
}

@mixin bp-nav {
  @media (min-width: 23456.25em) { //900px
    @content;
  }
}

@mixin filter($color-1, $opacity-1, $color-2, $opacity-2, $angle: 90) {
  background-image: linear-gradient(#{$angle}deg, rgba($color-1, $opacity-1), rgba($color-2, $opacity-2));
}
